import React, { useEffect, useRef } from 'react';

interface Particle {
  x: number;
  y: number;
  size: number;
  speedX: number;
  speedY: number;
  color: string;
  alpha: number;
  alphaSpeed: number;
}

const ParticleBackground: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const particlesRef = useRef<Particle[]>([]);
  
  // 生成粒子
  const createParticles = (width: number, height: number) => {
    const particles: Particle[] = [];
    const particleCount = Math.min(Math.floor(width * height / 8000), 150);
    
    for (let i = 0; i < particleCount; i++) {
      const randomAlpha = 0.4 + Math.random() * 0.3;
      particles.push({
        x: Math.random() * width,
        y: Math.random() * height,
        size: Math.random() * 4 + 1,
        speedX: Math.random() * 0.5 - 0.25,
        speedY: Math.random() * 0.5 - 0.25,
        color: getRandomColor(),
        alpha: randomAlpha,
        alphaSpeed: 0.002 + Math.random() * 0.005
      });
    }
    
    return particles;
  };

  // 生成明亮的随机颜色
  const getRandomColor = () => {
    const colors = [
      '#1E88E5', // 主要蓝色
      '#29B6F6', // 天蓝色
      '#4FC3F7', // 浅蓝色
      '#FFC107', // 阳光黄色
      '#FFD54F', // 浅黄色
      '#FF9800', // 橙色
      '#4CAF50', // 绿色
      '#81C784', // 浅绿色
      '#673AB7', // 紫色 - 少量点缀
    ];
    return colors[Math.floor(Math.random() * colors.length)];
  };

  // 更新粒子位置
  const updateParticles = (particles: Particle[], width: number, height: number) => {
    return particles.map(particle => {
      // 边界检查和位置更新
      if (particle.x < 0 || particle.x > width) particle.speedX *= -1;
      if (particle.y < 0 || particle.y > height) particle.speedY *= -1;

      // 更新透明度产生闪烁效果
      particle.alpha += particle.alphaSpeed;
      if (particle.alpha >= 0.7 || particle.alpha <= 0.3) {
        particle.alphaSpeed *= -1;
      }
      
      return {
        ...particle,
        x: particle.x + particle.speedX,
        y: particle.y + particle.speedY
      };
    });
  };

  // 绘制粒子
  const drawParticles = (
    ctx: CanvasRenderingContext2D, 
    particles: Particle[], 
    width: number, 
    height: number,
    mouseX: number | null = null,
    mouseY: number | null = null
  ) => {
    ctx.clearRect(0, 0, width, height);
    
    // 绘制粒子
    particles.forEach(particle => {
      ctx.beginPath();
      ctx.arc(particle.x, particle.y, particle.size, 0, Math.PI * 2);
      
      // 创建径向渐变
      const gradient = ctx.createRadialGradient(
        particle.x, particle.y, 0,
        particle.x, particle.y, particle.size * 2
      );
      
      const color = particle.color;
      const rgbColor = hexToRgb(color);
      
      gradient.addColorStop(0, `rgba(${rgbColor.r}, ${rgbColor.g}, ${rgbColor.b}, ${particle.alpha})`);
      gradient.addColorStop(1, `rgba(${rgbColor.r}, ${rgbColor.g}, ${rgbColor.b}, 0)`);
      
      ctx.fillStyle = gradient;
      ctx.fill();
    });
    
    // 连接临近的粒子
    for (let i = 0; i < particles.length; i++) {
      for (let j = i + 1; j < particles.length; j++) {
        const dx = particles[i].x - particles[j].x;
        const dy = particles[i].y - particles[j].y;
        const distance = Math.sqrt(dx * dx + dy * dy);
        
        if (distance < 150) {
          ctx.beginPath();
          
          // 基于粒子颜色创建连线
          const color1 = hexToRgb(particles[i].color);
          const color2 = hexToRgb(particles[j].color);
          
          // 混合两个粒子的颜色
          const r = Math.floor((color1.r + color2.r) / 2);
          const g = Math.floor((color1.g + color2.g) / 2);
          const b = Math.floor((color1.b + color2.b) / 2);
          
          const alpha = 0.2 * (1 - distance / 150);
          ctx.strokeStyle = `rgba(${r}, ${g}, ${b}, ${alpha})`;
          
          ctx.lineWidth = 1 * (1 - distance / 150);
          ctx.moveTo(particles[i].x, particles[i].y);
          ctx.lineTo(particles[j].x, particles[j].y);
          ctx.stroke();
        }
      }
      
      // 连接鼠标和粒子
      if (mouseX !== null && mouseY !== null) {
        const dx = particles[i].x - mouseX;
        const dy = particles[i].y - mouseY;
        const distance = Math.sqrt(dx * dx + dy * dy);
        
        if (distance < 200) {
          ctx.beginPath();
          const color = hexToRgb(particles[i].color);
          
          ctx.strokeStyle = `rgba(${color.r}, ${color.g}, ${color.b}, ${0.3 * (1 - distance / 200)})`;
          ctx.lineWidth = 1.5 * (1 - distance / 200);
          ctx.moveTo(particles[i].x, particles[i].y);
          ctx.lineTo(mouseX, mouseY);
          ctx.stroke();
          
          // 鼠标悬停时增加粒子速度
          particles[i].speedX += (Math.random() - 0.5) * 0.05;
          particles[i].speedY += (Math.random() - 0.5) * 0.05;
          
          // 限制最大速度
          particles[i].speedX = Math.min(Math.max(particles[i].speedX, -1.2), 1.2);
          particles[i].speedY = Math.min(Math.max(particles[i].speedY, -1.2), 1.2);
        }
      }
    }
  };
  
  // Hex颜色转RGB
  const hexToRgb = (hex: string) => {
    // 移除#前缀如果存在
    hex = hex.replace(/^#/, '');
    
    // 解析十六进制颜色
    const bigint = parseInt(hex, 16);
    const r = (bigint >> 16) & 255;
    const g = (bigint >> 8) & 255;
    const b = bigint & 255;
    
    return { r, g, b };
  };

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    
    const ctx = canvas.getContext('2d');
    if (!ctx) return;
    
    let animationFrameId: number;
    let mouseX: number | null = null;
    let mouseY: number | null = null;
    
    // 调整canvas大小
    const handleResize = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      particlesRef.current = createParticles(canvas.width, canvas.height);
    };
    
    // 鼠标移动跟踪
    const handleMouseMove = (e: MouseEvent) => {
      const rect = canvas.getBoundingClientRect();
      mouseX = e.clientX - rect.left;
      mouseY = e.clientY - rect.top;
    };
    
    // 鼠标离开
    const handleMouseLeave = () => {
      mouseX = null;
      mouseY = null;
    };
    
    // 动画循环
    const animate = () => {
      if (!ctx) return;
      
      particlesRef.current = updateParticles(
        particlesRef.current, 
        canvas.width, 
        canvas.height
      );
      
      drawParticles(
        ctx, 
        particlesRef.current, 
        canvas.width, 
        canvas.height,
        mouseX,
        mouseY
      );
      
      animationFrameId = requestAnimationFrame(animate);
    };
    
    // 初始化
    handleResize();
    window.addEventListener('resize', handleResize);
    canvas.addEventListener('mousemove', handleMouseMove);
    canvas.addEventListener('mouseleave', handleMouseLeave);
    
    // 启动动画
    animate();
    
    // 清理
    return () => {
      window.removeEventListener('resize', handleResize);
      canvas.removeEventListener('mousemove', handleMouseMove);
      canvas.removeEventListener('mouseleave', handleMouseLeave);
      cancelAnimationFrame(animationFrameId);
    };
  }, []);

  return (
    <canvas
      ref={canvasRef}
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        zIndex: -1,
        opacity: 0.8,
        pointerEvents: 'none'
      }}
    />
  );
};

export default ParticleBackground; 